import React from "react";
import "./Contact.css";
function Contact() {
  return (
    <div>
      <div className="contact-header" id="contact">
        Contact Us
      </div>
      <div className="contact-box">
        <div className="left-icon">
          <div>
            <img src={`${process.env.PUBLIC_URL}/image/location.svg`} alt="" />
            <div className="name">
              <h3>Location:</h3>
              <span>Gurgaon</span>
            </div>
          </div>
          <div>
            <img src={`${process.env.PUBLIC_URL}/image/gmail.svg`} alt="" />
            <div className="name">
              <h3>Email:</h3>
              <span>email</span>
            </div>
          </div>
          <div>
            <img src={`${process.env.PUBLIC_URL}/image/call.svg`} alt="" />
            <div className="name">
              <h3>Call:</h3>
              <a href="tel:+91-8683818621">
                <span>+91-8683818621</span>
              </a>
            </div>
          </div>
        </div>
        <div className="form">
          <div className="name">
            <input type="text" placeholder="Name" />
            <input type="text" placeholder="Email" />
          </div>
          <input type="text" placeholder="Subject" />
          <input type="text" placeholder="Message" />
          <button>Send</button>
        </div>
      </div>
      <div className="contact-svg">
        <img src={`${process.env.PUBLIC_URL}/image/contact.svg`} alt="" />
      </div>
    </div>
  );
}

export default Contact;
