import React from "react";
import "./Nav.css";
function toggleNav(event) {
  let icon = "cross";
  if (/cross/.test(event.target.src)) {
    icon = "rectangle";
    event.currentTarget.parentElement.children[1].classList.remove("active");
  } else {
    event.currentTarget.parentElement.children[1].classList.add("active");
  }
  event.target.src = `${process.env.PUBLIC_URL}/image/${icon}.svg`;
}
function Nav() {
  return (
    <div className="nav-box">
      <div className="logo"></div>
      <div className="nav">
        <a href="#" className="link">
          HOME
        </a>
        <a
          href="https://play.google.com/store/apps/details?id=greenpaper.dopagentapp.dopagentsoftware.indiapost"
          className="link"
        >
          DOWNLOAD ANDROID APP
        </a>
        <a href="#contact" className="link">
          CONTACT DOP AGENT
        </a>
      </div>
      <button onClick={toggleNav}>
        <img
          src={`${process.env.PUBLIC_URL}/image/rectangle.svg`}
          alt=""
          srcset=""
        />
      </button>
    </div>
  );
}

export default Nav;
