import React from "react";
import "./Privacy.css";
function Privacy() {
  return (
    <div>
      <div className="privacy-header"> Privacy Policy</div>
      <div className="privacy">
        <h2>Welcome To Privacy Policy</h2>
        <p>
          DOP AGENT APP is committed to maintaining the confidentiality,
          integrity and security of all personal identifiable information about
          our users. This Privacy Policy explains how we protect personally
          identifiable information provided through our website. We do not share
          any of your information with third parties. We do not sell, trade, or
          otherwise transfer to outside parties your personally identifiable
          information. None of your information including your performance on
          tests will be shared with any third party. You are responsible for
          maintaining the confidentiality of your Username, Registered email and
          password If you believe that they have been stolen or been made known
          to others, you must contact us immediately at hr@gmail.com, but in any
          event you should change your password immediately. We are not
          responsible if someone else accesses your account through Registration
          Information they have obtained from you or through a violation by you
          of this Privacy Policy. By registering on our site, you consent to our
          privacy policy. Changes to our Privacy Policy If we decide to change
          our privacy policy, we will post those changes on this page.
        </p>
      </div>
      <div className="privacy-footer">
        <img src={`${process.env.PUBLIC_URL}/image/privacy.svg`} alt="" />
        <div className="footer">
          <div className="social">
            <img src={`${process.env.PUBLIC_URL}/image/logo.png`} alt="" />
            <div className="social-icons">
              <a href="#">
                <img
                  src={`${process.env.PUBLIC_URL}/image/linkedin.svg`}
                  alt=""
                />
              </a>
              <a href="">
                <img
                  src={`${process.env.PUBLIC_URL}/image/facebook.svg`}
                  alt=""
                />
              </a>
              <a href="#">
                <img
                  src={`${process.env.PUBLIC_URL}/image/twitter.svg`}
                  alt=""
                />
              </a>
            </div>
            <p>
              © Copyright Dop Agent App 2021 Department Of Post Agent Software
              App - All Rights Reserved.
            </p>
          </div>
          <div className="download-card">
            <div className="header">
              <h3>Download DOP AGENT App</h3>
              <p>Join the network of 10K+ members today.</p>
            </div>
            <div className="input">
              <span className="ext">+91</span>
              <input type="text" placeholder="Enter Mobile Number" />
              <button>Send Link</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Privacy;


