import React from "react";
import "./Offer.css";
function Offer() {
  return (
    <div>
      <div className="offer-header">What do we offer?</div>
      <div className="features">
        {/* <div className="first-row"> */}
          <div className="feature">
            <img
              src={`${process.env.PUBLIC_URL}/image/features1.svg`}
              alt="cloud"
            />
            <br />
            <span>Sell Insurance</span>
          </div>
          <div className="feature">
            <img
              src={`${process.env.PUBLIC_URL}/image/features2.svg`}
              alt="cloud"
            />
            <br />
            <span>Get rewards timely</span>
          </div>
          <div className="feature">
            <img
              src={`${process.env.PUBLIC_URL}/image/features3.svg`}
              alt="cloud"
            />
            <br />
            <span>Make RD account <br/> in 10 secs! </span>
          </div>
        {/* </div> */}
        {/* <div className="second-row"> */}
          <div className="feature">
            <img
              src={`${process.env.PUBLIC_URL}/image/features4.svg`}
              alt="cloud"
            />
            <br />
            <span>Become an Agent</span>
          </div>
          <div className="feature">
            <img
              src={`${process.env.PUBLIC_URL}/image/features5.svg`}
              alt="cloud"
            />
            <br />
            <span>Sell Loan</span>
          </div>
          <div className="feature">
            <img
              src={`${process.env.PUBLIC_URL}/image/features6.svg`}
              alt="cloud"
            />
            <br />
            <span>Refer and Earn </span>
          </div>
        {/* </div> */}
      </div>
    </div>
  );
}

export default Offer;
