import React from "react";

import "./Home.css";

import Nav from "../nav/Nav.js";
import Offer from "../offer/Offer.js";
import Download from "../download/Download.js";
import Testimonial from "../testimonial/Testimonial.js";
import About from "../about/About.js";
import Contact from "../contact/Contact.js";
import Privacy from "../privacy/Privacy.js";

function Home() {
  return (
    <div>
      <Nav />
      <div className="box">
        <div className="intro">
          <div className="left-svg">
            <img
              src={`${process.env.PUBLIC_URL}/image/left-cloud.svg`}
              alt="cloud"
            />
          </div>
          <div className="text">
            <h1>
              Use DOP AGENT APP to create RD lots,
              <br /> save RD data in mobile
            </h1>
            <h3>Trusted by 10 lacs+ agents</h3>
          </div>
          <div className="right-svg">
            <img
              src={`${process.env.PUBLIC_URL}/image/right-cloud.svg`}
              alt="cloud"
            />
          </div>
        </div>
        <div className="download">
          <div className="input">
            <div>
              <img
                src={`${process.env.PUBLIC_URL}/image/india-map.svg`}
                alt=""
              />
              <span>+91</span>
              <input type="text" placeholder="Enter Mobile Number" />
              <img src={`${process.env.PUBLIC_URL}/image/arrow.svg`} alt="" />
            </div>
            <button>SEND LINK</button>
          </div>
          <span>Or</span>
          <a href="https://play.google.com/store/apps/details?id=greenpaper.dopagentapp.dopagentsoftware.indiapost">
            <img
              src={`${process.env.PUBLIC_URL}/image/google.svg`}
              alt=""
              srcset=""
            />
          </a>
        </div>
        <div className="post-office">
          <img src={`${process.env.PUBLIC_URL}/image/Tree.svg`} alt="tree" />
          <img
            src={`${process.env.PUBLIC_URL}/image/post_office.svg`}
            alt="tree"
          />
        </div>
      </div>
      <Offer />
      <Download />
      <Testimonial />
      <About />
      <Contact />
      <Privacy />
    </div>
  );
}

export default Home;
