import React from 'react'
import "./Download.css"
function download() {
    return (
      <div className="box">
        <div className="download-header">
          10000+ downloads on Play store and still growing !!!!
        </div>
        <div className="mobile-box">
          <div className="mobile">
            <div className="left">
              <img
                src={`${process.env.PUBLIC_URL}/image/left-phone.svg`}
                alt=""
              />
            </div>
            <div className="mid">
              <img
                src={`${process.env.PUBLIC_URL}/image/mid-phone.svg`}
                alt=""
              />
              <div className="download">
                <h1>Download it now.</h1>
                <a href="https://play.google.com/store/apps/details?id=greenpaper.dopagentapp.dopagentsoftware.indiapost">
                  <img
                    src={`${process.env.PUBLIC_URL}/image/google.svg`}
                    alt=""
                  />
                </a>
              </div>
            </div>
            <div className="right">
              <img
                src={`${process.env.PUBLIC_URL}/image/right-phone.svg`}
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    );
}

export default download
